'use client';

import { useEffect, useRef, useState } from 'react';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

if (typeof window !== 'undefined') {
  gsap.registerPlugin(ScrollTrigger);
}

export default function ComingSoonContent() {
  const containerRef = useRef<HTMLDivElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const titleRef = useRef<HTMLHeadingElement>(null);
  const [email, setEmail] = useState('');
  const [submitted, setSubmitted] = useState(false);
  const [countdown, setCountdown] = useState({ days: 0, hours: 0, minutes: 0, seconds: 0 });
  const [mousePos, setMousePos] = useState({ x: 0.5, y: 0.5 });
  const [isMobile, setIsMobile] = useState(false);

  const launchDate = new Date('2025-03-01T00:00:00').getTime();

  useEffect(() => {
    const checkMobile = () => setIsMobile(window.innerWidth < 768);
    checkMobile();
    window.addEventListener('resize', checkMobile);
    return () => window.removeEventListener('resize', checkMobile);
  }, []);

  useEffect(() => {
    const timer = setInterval(() => {
      const now = new Date().getTime();
      const distance = launchDate - now;
      setCountdown({
        days: Math.floor(distance / (1000 * 60 * 60 * 24)),
        hours: Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)),
        minutes: Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60)),
        seconds: Math.floor((distance % (1000 * 60)) / 1000),
      });
    }, 1000);
    return () => clearInterval(timer);
  }, [launchDate]);

  // Revolutionary mesh gradient effect
  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext('2d', { alpha: false });
    if (!ctx) return;

    const resize = () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
    };
    resize();
    window.addEventListener('resize', resize);

    let time = 0;
    const gridSize = isMobile ? 8 : 12;
    const points: Array<{ x: number; y: number; baseX: number; baseY: number; vx: number; vy: number }> = [];

    for (let i = 0; i <= gridSize; i++) {
      for (let j = 0; j <= gridSize; j++) {
        const x = (i / gridSize) * canvas.width;
        const y = (j / gridSize) * canvas.height;
        points.push({
          x,
          y,
          baseX: x,
          baseY: y,
          vx: (Math.random() - 0.5) * 0.5,
          vy: (Math.random() - 0.5) * 0.5,
        });
      }
    }

    let animationId: number;

    const animate = () => {
      time += 0.003;

      const gradient = ctx.createLinearGradient(0, 0, canvas.width, canvas.height);
      gradient.addColorStop(0, '#000000');
      gradient.addColorStop(0.5, '#0a0a0a');
      gradient.addColorStop(1, '#000000');
      ctx.fillStyle = gradient;
      ctx.fillRect(0, 0, canvas.width, canvas.height);

      // Update points
      points.forEach((point, i) => {
        const waveX = Math.sin(time + point.baseY * 0.002) * 60;
        const waveY = Math.cos(time + point.baseX * 0.002) * 60;

        const mouseInfluence = isMobile ? 0 : 150;
        const dx = (mousePos.x * canvas.width) - point.baseX;
        const dy = (mousePos.y * canvas.height) - point.baseY;
        const dist = Math.sqrt(dx * dx + dy * dy);
        const force = Math.max(0, 1 - dist / mouseInfluence);

        point.x = point.baseX + waveX + dx * force * 0.3;
        point.y = point.baseY + waveY + dy * force * 0.3;
      });

      // Draw mesh
      for (let i = 0; i < gridSize; i++) {
        for (let j = 0; j < gridSize; j++) {
          const idx = i * (gridSize + 1) + j;
          const p1 = points[idx];
          const p2 = points[idx + 1];
          const p3 = points[idx + gridSize + 2];
          const p4 = points[idx + gridSize + 1];

          if (!p1 || !p2 || !p3 || !p4) continue;

          const centerX = (p1.x + p2.x + p3.x + p4.x) / 4;
          const centerY = (p1.y + p2.y + p3.y + p4.y) / 4;

          const distFromCenter = Math.sqrt(
            Math.pow(centerX - canvas.width / 2, 2) +
            Math.pow(centerY - canvas.height / 2, 2)
          );
          const maxDist = Math.sqrt(Math.pow(canvas.width / 2, 2) + Math.pow(canvas.height / 2, 2));
          const intensity = 1 - (distFromCenter / maxDist);

          const hue = (time * 10 + i * 5 + j * 5) % 360;
          const alpha = intensity * 0.15;

          const grd = ctx.createRadialGradient(centerX, centerY, 0, centerX, centerY, 200);
          grd.addColorStop(0, `hsla(${hue}, 70%, 60%, ${alpha})`);
          grd.addColorStop(1, `hsla(${hue}, 70%, 40%, 0)`);

          ctx.fillStyle = grd;
          ctx.beginPath();
          ctx.moveTo(p1.x, p1.y);
          ctx.lineTo(p2.x, p2.y);
          ctx.lineTo(p3.x, p3.y);
          ctx.lineTo(p4.x, p4.y);
          ctx.closePath();
          ctx.fill();

          // Subtle grid lines
          ctx.strokeStyle = `rgba(255, 255, 255, ${intensity * 0.03})`;
          ctx.lineWidth = 1;
          ctx.stroke();
        }
      }

      animationId = requestAnimationFrame(animate);
    };

    animate();

    return () => {
      cancelAnimationFrame(animationId);
      window.removeEventListener('resize', resize);
    };
  }, [mousePos, isMobile]);

  // Ultra-smooth animations
  useEffect(() => {
    const ctx = gsap.context(() => {
      const tl = gsap.timeline({ defaults: { ease: 'power4.out' } });

      tl.from(containerRef.current, {
        opacity: 0,
        duration: 1.5,
      });

      if (titleRef.current) {
        const text = titleRef.current.textContent || '';
        const words = text.split(' ');
        titleRef.current.innerHTML = words.map(word =>
          `<span class="word inline-block overflow-hidden"><span class="inner inline-block">${word}</span></span>`
        ).join(' ');

        tl.from('.word .inner', {
          yPercent: 100,
          opacity: 0,
          stagger: 0.1,
          duration: 1.2,
          ease: 'power4.out',
        }, 0.3);
      }

      gsap.from('.reveal-up', {
        scrollTrigger: {
          trigger: '.reveal-up',
          start: 'top 80%',
        },
        y: 60,
        opacity: 0,
        stagger: 0.15,
        duration: 1.2,
        ease: 'power4.out',
      });

    }, containerRef);

    return () => ctx.revert();
  }, []);

  useEffect(() => {
    if (isMobile) return;

    const handleMove = (e: MouseEvent) => {
      setMousePos({
        x: e.clientX / window.innerWidth,
        y: e.clientY / window.innerHeight,
      });
    };

    window.addEventListener('mousemove', handleMove);
    return () => window.removeEventListener('mousemove', handleMove);
  }, [isMobile]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setSubmitted(true);
  };

  return (
    <div ref={containerRef} className="relative min-h-screen bg-black text-white overflow-hidden">
      <canvas ref={canvasRef} className="fixed inset-0 w-full h-full" />

      <div className="relative z-10">
        {/* Hero */}
        <div className="min-h-screen flex flex-col items-center justify-center px-6 sm:px-8 md:px-12">
          <div className="max-w-7xl w-full mx-auto text-center space-y-12 sm:space-y-16 md:space-y-20">

            {/* Badge */}
            <div className="inline-flex items-center gap-3 px-6 py-2.5 rounded-full border border-white/10 backdrop-blur-sm">
              <div className="w-2 h-2 rounded-full bg-white animate-pulse" />
              <span className="text-xs sm:text-sm tracking-[0.2em] uppercase font-medium text-white/70">Launching March 2025</span>
            </div>

            {/* Title */}
            <h1 ref={titleRef} className="text-5xl sm:text-7xl md:text-8xl lg:text-9xl font-light tracking-tight leading-none">
              MasseurMatch
            </h1>

            {/* Subtitle */}
            <p className="text-lg sm:text-xl md:text-2xl text-white/60 max-w-3xl mx-auto font-light leading-relaxed">
              Connect with verified massage therapists.<br className="hidden sm:block" />
              Premium wellness. Simplified.
            </p>

            {/* Countdown */}
            <div className="grid grid-cols-4 gap-4 sm:gap-6 md:gap-8 max-w-4xl mx-auto">
              {[
                { label: 'Days', value: countdown.days },
                { label: 'Hours', value: countdown.hours },
                { label: 'Mins', value: countdown.minutes },
                { label: 'Secs', value: countdown.seconds },
              ].map((item) => (
                <div key={item.label} className="group">
                  <div className="relative">
                    <div className="absolute inset-0 bg-white/5 rounded-2xl blur-xl" />
                    <div className="relative bg-white/5 backdrop-blur-md border border-white/10 rounded-2xl p-4 sm:p-6 md:p-8 hover:bg-white/10 transition-colors duration-500">
                      <div className="text-3xl sm:text-4xl md:text-6xl lg:text-7xl font-extralight tabular-nums mb-2">
                        {String(item.value).padStart(2, '0')}
                      </div>
                      <div className="text-[0.65rem] sm:text-xs text-white/40 uppercase tracking-[0.2em] font-medium">
                        {item.label}
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>

            {/* Form */}
            {!submitted ? (
              <form onSubmit={handleSubmit} className="max-w-2xl mx-auto">
                <div className="relative group">
                  <div className="absolute inset-0 bg-white/5 rounded-2xl blur-xl" />
                  <div className="relative flex flex-col sm:flex-row gap-3 bg-white/5 backdrop-blur-md border border-white/10 rounded-2xl p-2 hover:bg-white/10 transition-colors duration-500">
                    <input
                      type="email"
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      placeholder="Enter your email"
                      required
                      className="flex-1 px-6 py-4 bg-transparent text-white placeholder-white/30 focus:outline-none text-base sm:text-lg font-light"
                    />
                    <button
                      type="submit"
                      className="px-8 py-4 bg-white text-black rounded-xl font-medium text-base sm:text-lg hover:bg-white/90 transition-colors duration-300 whitespace-nowrap"
                    >
                      Join Waitlist
                    </button>
                  </div>
                </div>
                <p className="mt-4 text-sm text-white/40">
                  <span className="text-white font-medium">2,847</span> people already signed up
                </p>
              </form>
            ) : (
              <div className="max-w-2xl mx-auto">
                <div className="relative group">
                  <div className="absolute inset-0 bg-white/5 rounded-2xl blur-xl" />
                  <div className="relative bg-white/5 backdrop-blur-md border border-white/10 rounded-2xl p-12 text-center">
                    <div className="w-16 h-16 mx-auto mb-6 rounded-full bg-white/10 flex items-center justify-center">
                      <svg className="w-8 h-8" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                      </svg>
                    </div>
                    <h3 className="text-2xl font-light mb-3">You're on the list</h3>
                    <p className="text-white/60">We'll notify you when we launch</p>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Features */}
        <div className="py-24 sm:py-32 md:py-40 px-6 sm:px-8 md:px-12">
          <div className="max-w-7xl mx-auto">
            <h2 className="text-3xl sm:text-4xl md:text-5xl font-light text-center mb-16 sm:mb-20 md:mb-24">
              Premium Wellness Platform
            </h2>

            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6 sm:gap-8">
              {[
                { title: 'AI Matching', desc: 'Smart algorithms find your perfect therapist' },
                { title: 'Instant Booking', desc: 'Real-time availability and confirmation' },
                { title: 'Verified Pros', desc: 'Licensed and background-checked experts' },
                { title: 'Direct Chat', desc: 'Connect and discuss your wellness goals' },
              ].map((feature, i) => (
                <div key={i} className="reveal-up group">
                  <div className="relative">
                    <div className="absolute inset-0 bg-white/5 rounded-2xl blur-xl opacity-0 group-hover:opacity-100 transition-opacity duration-500" />
                    <div className="relative bg-white/5 backdrop-blur-md border border-white/10 rounded-2xl p-8 hover:bg-white/10 transition-all duration-500">
                      <div className="w-12 h-12 mb-6 rounded-xl bg-white/10 flex items-center justify-center">
                        <div className="w-6 h-6 rounded-full bg-white/20" />
                      </div>
                      <h3 className="text-xl font-medium mb-3">{feature.title}</h3>
                      <p className="text-white/60 text-sm leading-relaxed font-light">{feature.desc}</p>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Stats */}
        <div className="py-24 sm:py-32 md:py-40 px-6 sm:px-8 md:px-12">
          <div className="max-w-5xl mx-auto">
            <div className="relative">
              <div className="absolute inset-0 bg-white/5 rounded-3xl blur-2xl" />
              <div className="relative bg-white/5 backdrop-blur-md border border-white/10 rounded-3xl p-12 sm:p-16 md:p-20">
                <div className="grid grid-cols-1 sm:grid-cols-3 gap-12 sm:gap-16">
                  {[
                    { value: '500+', label: 'Verified Therapists' },
                    { value: '10+', label: 'Major Cities' },
                    { value: '100%', label: 'Premium Quality' },
                  ].map((stat, i) => (
                    <div key={i} className="reveal-up text-center">
                      <div className="text-5xl sm:text-6xl md:text-7xl font-extralight mb-4">{stat.value}</div>
                      <div className="text-white/60 text-sm uppercase tracking-[0.2em] font-medium">{stat.label}</div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Footer */}
        <div className="py-12 px-6 text-center">
          <p className="text-white/30 text-sm">Â© 2025 MasseurMatch. All rights reserved.</p>
        </div>
      </div>
    </div>
  );
}
